.container {
  max-width: 1200px;
  padding: 0 1.5rem;
  margin: 0 auto;
  box-sizing: border-box;
}

.wrap-background-image {
  position: relative;
}
.wrap-background-image .background-image {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.wrap-background-image .background-image img {
  display: none;
}

.regulation {
  padding: 5rem 0;
}
.regulation .title > h2 {
  font-size: 1.5rem;
  line-height: 1.5;
  font-family: "Poppins Regular";
  font-weight: 600;
  color: #111;
  margin-bottom: 1.25rem;
}
.regulation .paragraph + .title {
  margin-top: 2.5rem;
}
.regulation .paragraph {
  color: #2e2e2e;
  max-width: 75ch;
  margin-bottom: 1.25rem;
}

.bikes .wrap {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 3.75rem 0;
  gap: 2.5rem;
}
.bikes .wrap .bike-image {
  display: grid;
}
.bikes .wrap .bike-image img {
  grid-area: 1/1;
}
.bikes .wrap .bike-image span {
  display: inline-block;
  place-self: start end;
  grid-area: 1/1;
  background: black;
  color: white;
  margin-top: 1.25rem;
  padding: 0.5rem 1rem 0.5rem 0.5rem;
  border-radius: 0.25rem 0 0 0.25rem;
}
.bikes .wrap .bike-content {
  display: grid;
  grid-template-columns: 1fr auto;
  align-content: start;
}
.bikes .wrap .bike-content .title, .bikes .wrap .bike-content .paragraph-content {
  grid-column: 1/-1;
}
.bikes .wrap .bike-content .title {
  margin-bottom: 1.25rem;
}
.bikes .wrap .bike-content .paragraph-content {
  margin-bottom: 2rem;
}
.bikes .wrap .bike-content .paragraph-content .paragraph {
  color: #595959;
}
.bikes .wrap .bike-content .title::before {
  content: "";
  display: block;
  width: 0.75rem;
  height: 0.5rem;
  background: #ffbb00;
  margin-bottom: 0.5rem;
}
.bikes .wrap .bike-content .button {
  place-self: end;
  display: flex;
  align-items: center;
}
.bikes .wrap .bike-content .button::after {
  content: "";
  display: inline-block;
  width: 1.125rem;
  height: 0.625rem;
  margin-left: 0.75rem;
  background: url("../images/icones/seta.svg");
  transition: transform 0.2s;
}
.bikes .wrap .bike-content .button:hover::after {
  transform: translateX(4px);
}
.bikes .wrap .bike-content .icon-description {
  display: grid;
  gap: 1rem;
  color: #595959;
}
.bikes .wrap .bike-content .icon-description li {
  display: flex;
  align-items: center;
}
.bikes .wrap .bike-content .icon-description li img {
  width: 1.5rem;
  margin-right: 0.5rem;
}
.bikes .wrapper:nth-child(even) {
  background: linear-gradient(to right, white 30%, #111 0%);
}
.bikes .wrapper:nth-child(even) .title {
  color: white;
}
.bikes .wrapper:nth-child(even) .paragraph-content .paragraph {
  color: #b2b2b2;
}
.bikes .wrapper:nth-child(even) .icon-description {
  color: #b2b2b2;
}
@media screen and (max-width: 767px) {
  .bikes .wrapper:nth-child(even) {
    background: #111;
  }
  .bikes .wrap {
    grid-template-columns: 1fr;
    gap: 1.25rem;
    padding: 2.5rem 0;
  }
}
@media screen and (max-width: 479px) {
  .bikes .wrap .bike-content {
    grid-template-columns: 1fr;
  }
  .bikes .wrap .bike-content .icon-description {
    margin-bottom: 2rem;
    grid-template-columns: 1fr 1fr;
  }
  .bikes .wrap .bike-content .button {
    place-self: start;
  }
}

.header {
